.global-nav{z-index:20;position:sticky;top:0;left:0;right:0;border-bottom-width:1px;border-bottom-style:solid;height:60px;background-color:inherit;backdrop-filter:blur(15px);border-color:var(--theme-border);background-color:var(--theme-bg)}.nav-wrapper{display:block}.nav-wrapper .logo{display:block;text-align:center;padding:1rem 0;font-size:1.5rem;font-weight:bold;color:inherit;line-height:normal}.nav-wrapper .toggler{z-index:20;position:absolute;top:0;right:0;display:grid;place-content:center;padding:1rem;height:60px;font-size:24px}.nav-wrapper .toggler::after{content:"î®¨";font-family:"boxicons"}.navbar{z-index:10;position:fixed;top:0;left:0;right:0;bottom:0;width:100%;height:100vh;display:flex;flex-direction:column;justify-content:center;color:var(--theme-color);background-color:var(--theme-bg)}.navbar .nav-link{z-index:10;display:block;padding:.5rem 1rem;font-size:1.25rem;text-align:center}.text-gradient-anima{background-color:#000;background-image:linear-gradient(50% 0%, 8% 50%, rgba(255, 255, 255, 0.5019607843) 0%, rgba(255, 255, 255, 0) 100%);color:rgba(0,0,0,0);-webkit-text-fill-color:rgba(0,0,0,0);background-clip:text;background-size:500px 100%;transition:background .5s ease-in-out;animation:textGradient 3s linear infinite}.tracking-in-expand{animation:tracking-in-expand .7s cubic-bezier(0.215, 0.61, 0.355, 1) both}@keyframes tracking-in-expand{0%{letter-spacing:-1em;opacity:0}40%{opacity:.6}100%{letter-spacing:.125em;opacity:1}}@keyframes textGradient{0%{background-position-x:500px}100%{background-position-x:1000px}}